import React, { useCallback } from 'react';
import { Helmet } from 'react-helmet';

import CustomBanner from '../../components/CustomBanner';
import ContactForm from '../../containers/ContactForm';
import Map from '../../components/Map';

import axios from '../../clients/axios';

const AddReviewPage = (_props) => {

    const onSendMessageHandler = useCallback(async (name, email, message) => {
        if(!name || !email || !message) return;
        await axios.post('/reviews', { client: name, email, review: message, approved: false });
    }, []);

    return (
        <React.Fragment>
             <Helmet>
                <meta charSet="utf-8" />
                <title>Laisser un commentaire | MS Renovation</title>
                <meta name="description" content="Laisser un commentaire" />
                <link rel="canonical" href="https://ms-renovation.fr/leave-review" />
            </Helmet>

            <CustomBanner title={'Laisser un commentaire'} url={"/leave-review"} />
            <ContactForm sendHandler={onSendMessageHandler} />
            <Map lat={48.927900} lon={2.367350} />
        </React.Fragment>
    )
};

export default AddReviewPage;