import React from 'react';

const Services = (_props) => {
    return (
        <section className="service-area section-gap" id="service">
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-md-12 pb-50 header-text text-center">
                        <h1 className="mb-10">Ce que nous proposons à nos clients</h1>
                        <p>
                            Nos clients sont au cœur de tout ce que nous faisons
                        </p>
                    </div>
                </div>
                <div className="row">
                    <div className="col-lg-4">
                        <div className="single-service">
                            <h4>Design d'intérieur</h4>
                            <p>
                                Nous vous accompagnons à chaque étape, dans un voyage qui mène à la création d'une maison qui est un reflet unique de vous.
                            </p>
                        </div>
                    </div>
                    <div className="col-lg-4">
                        <div className="single-service">
                            <h4>Conception d'architecture</h4>
                            <p>
                                Nous voulons que vous vous retrouviez avec une maison élégante qui résistera à l'épreuve du temps
                            </p>
                        </div>
                    </div>
                    <div className="col-lg-4">
                        <div className="single-service">
                            <h4>Concept de design</h4>
                            <p>
                            Vous passez beaucoup de temps chez vous, vous devez vous assurer que vous l'aimez là-bas
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default Services;