import React, { Component } from 'react';
import ImageUploader from 'react-images-upload';

import CustomBanner from '../../components/CustomBanner';
import axios from '../../clients/axios';
import './index.css';

class AddImagesPage extends Component {

    state = {
        images: [],
        uploaded: false
    }

    onDropHandler = (_imageFiles, imageDataUrls) => {
        this.setState({
            images: imageDataUrls
        });
    }

    onSubmitImagesHandler = async () => {
        const { images } = this.state;
        if (!images.length) return;

        try {
            await axios.post('/images', { images: this.state.images });
            this.setState({ uploaded: true });
        } catch (e) { }
    }

    render() {
        return (
            <React.Fragment>
                <CustomBanner title={'Ajouter des images'} url={"/admin/add-images"} />
                <div><br /></div>

                {
                    this.state.uploaded
                        ?
                        <div className="add-images-button" >
                            <a href="/gallery" className="genric-btn primary">Voir les images</a>
                        </div>
                        :
                        <React.Fragment>
                            <div className="add-images-button" >
                                <button onClick={this.onSubmitImagesHandler} className="genric-btn success">Importer des images</button>
                            </div>
                            <div><br /></div>

                            <ImageUploader
                                withIcon={true}
                                buttonText='Choisissez des images'
                                onChange={this.onDropHandler}
                                imgExtension={['.jpg', '.gif', '.png', '.gif']}
                                maxFileSize={5242880}
                                withPreview={true}
                            />
                        </React.Fragment>
                }

            </React.Fragment>
        );
    }
};

export default AddImagesPage;