import React, { Component } from 'react';

class ContactForm extends Component {
    state = {
        name: '',
        email: '',
        message: ''
    }

    onInputChangeHandler = e => {
        this.setState({ [e.target.name]: e.target.value });
    }

    onSendMessageHandler = async _e => {
        const { name, email, message } = this.state;        
        try {
            if(!name || !email || !message) return;

            await this.props.sendHandler(name, email, message);
            this.setState({ name:'', email:'', message:'' });
        } catch(e) {}
    }

    render() {

        return (
            <section className="contact-page-area section-gap">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-4 d-flex flex-column address-wrap">
                            <div className="single-contact-address d-flex flex-row">
                                <div className="icon">
                                    <span className="lnr lnr-home"></span>
                                </div>
                                <div className="contact-details">
                                    <h5>Saint Denis, France</h5>
                                    <p>36 Villa Thierry 93200</p>
                                </div>
                            </div>
                            <div className="single-contact-address d-flex flex-row">
                                <div className="icon">
                                    <span className="lnr lnr-phone-handset"></span>
                                </div>
                                <div className="contact-details">
                                    <h5>0983568616</h5>
                                    <p>Lundi - Vendredi 09h-17h</p>
                                </div>
                            </div>
                            <div className="single-contact-address d-flex flex-row">
                                <div className="icon">
                                    <span className="lnr lnr-envelope"></span>
                                </div>
                                <div className="contact-details">
                                    <h5>msrenovation@yahoo.fr</h5>
                                    <p>Contactez-nous à tout moment!</p>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-8">
                            <div className="form-area contact-form text-right " id="myForm">
                                <div className="row">
                                    <div className="col-lg-6 form-group">
                                        <input name="name"
                                            placeholder="Votre nom"
                                            className="common-input mb-20 form-control" 
                                            required
                                            type="text"
                                            autoComplete="off"
                                            value={this.state.name}
                                            onChange={this.onInputChangeHandler} />

                                        <input name="email"
                                            placeholder="Votre adresse email"
                                            pattern="[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{1,63}$"
                                            className="common-input mb-20 form-control"
                                            required
                                            type="email"
                                            autoComplete="off"
                                            value={this.state.email}
                                            onChange={this.onInputChangeHandler} />
                                    </div>
                                    <div className="col-lg-6 form-group">
                                        <textarea name="message"
                                            className="common-textarea form-control"
                                            placeholder="Votre message"
                                            value={this.state.message}
                                            onChange={this.onInputChangeHandler}>
                                        </textarea>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="alert-msg" style={{ textAlign: "left" }}></div>
                                        <button className="genric-btn primary circle"
                                            style={{ float: "right" }}
                                            onClick={this.onSendMessageHandler}>
                                            Envoyer le message
                                            </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
};

export default ContactForm;