import React from 'react';
import { Link } from 'react-router-dom';

const CallToAction = (_props) => {
    return (
        <React.Fragment>
            <section className="callto-action-area pt-120">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="callto-action-wrap col-lg-12 relative section-gap">
                            <div className="content">
                                <h1>
                                    La recherche d'un <br />
                                    design d'intérieur de qualité et abordable?
                                </h1>
                                <p className="mx-auto">
                                    Notre équipe de professionnels travaille de manière efficace et soucieuse du temps pour créer la rénovation de vos rêves.
                                </p>
                                <Link to={'/contact'} className="primary-btn text-uppercase">Contactez nous</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            {/* IKONICE ZA KLIJENTE */}
            <section className="brands-area pb-10 pt-10"></section>
        </React.Fragment>
    );
};

export default CallToAction;