import React from 'react';
import { Helmet } from 'react-helmet';

import Banner from '../../components/Banner';
import Services from '../../components/Services';
import ClientReviews from '../../containers/ClientReviews';
import Features from '../../containers/Features';
import CallToAction from '../../components/CallToAction';

const MainPage = (_props) => {
    return (
        <React.Fragment>
             <Helmet>
                <meta charSet="utf-8" />
                <title>MS Renovation</title>
                <meta name="description" content="Entreprise de rénovation expérimentée." />
                <meta name="keywords" content="ms, renovation, apartment, paris, france, service, qualité, fr, design, intérieur, galerie, images, gallery" />
                <link rel="canonical" href="https://ms-renovation.fr" />
            </Helmet>
            <Banner />
            <Services />
            <Features />
            <ClientReviews />
            <CallToAction />
        </React.Fragment>
    )
};

export default MainPage;