import React, { useCallback } from 'react';
import { Helmet } from 'react-helmet';

import CustomBanner from '../../components/CustomBanner';
import ContactForm from '../../containers/ContactForm';
import Map from '../../components/Map';

import axios from '../../clients/axios';

const ContactPage = (_props) => {

    const onSendMessageHandler = useCallback(async (name, email, message) => {
        if (!name || !email || !message) return;
        await axios.post('/contact', { name, email, message, subject: "MS Renovation Contact Message" });
    }, []);

    return (
        <React.Fragment>
            <Helmet>
                <meta charSet="utf-8" />
                <title>Contact | MS Renovation</title>
                <meta name="description" content="Contactez-nous à tout moment." />
                <meta name="keywords" content="ms, contactez, nous, renovation, apartment, paris, france, service, qualité, fr, design, intérieur, galerie, images, contact" />
                <link rel="canonical" href="https://ms-renovation.fr/contact" />
            </Helmet>

            <CustomBanner title={'Contact'} />
            <ContactForm sendHandler={onSendMessageHandler} />
            <Map lat={48.927900} lon={2.367350} />
        </React.Fragment>
    )
};

export default ContactPage;