import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';

import MainPage from '../../pages/Main';
import ContactPage from '../../pages/Contact';
import ReviewsPage from '../../pages/Reviews';
import AddReviewPage from '../../pages/AddReview';
import ApproveReviewsPage from '../../pages/ApproveReviewsPage';
import GalleryPage from '../../pages/Gallery';
import AddImagesPage from '../../pages/AddImages';

const NavRouter = (_props) => {
    return (
        <Switch>
            <Route exact path="/" component={ MainPage }/>
            <Route exact path="/contact" component={ ContactPage }/>
            <Route exact path="/leave-review" component={ AddReviewPage }/>
            <Route exact path="/reviews" component={ ReviewsPage }/>
            <Route exact path="/gallery" component={ GalleryPage }/>
            <Route exact path="/admin/approve-reviews" component={ ApproveReviewsPage }/>
            <Route exact path="/admin/add-images" component={ AddImagesPage }/>
            <Redirect from="*" to="/" />
        </Switch>
    );
}

export default NavRouter;