import React, { Component } from 'react';
import { Helmet } from 'react-helmet';

import './index.css';
import Pagination from "react-js-pagination";
import ImageGallery from 'react-image-gallery';
import CustomBanner from '../../components/CustomBanner';

import axios from '../../clients/axios';


class GalleryPage extends Component {
    state = {
        images: [],
        albums: [],
        activePage: 1
    }

    async componentDidMount() {
        try {
            const response = await axios.get('/images');

            this.setState({ albums: response.data });

            const images = this.mapImages(response.data[0].imageUrls);
            this.setState({ images });
        } catch (e) {
            console.log(e);
        }
    }

    mapImages = (imageUrls) => {
        return imageUrls.map(imageUrl => {
            return {
                original: imageUrl
            }
        })
    }

    onPageChangeHandler = (pageNumber) => {
        this.setState({ activePage: pageNumber });

        const images = this.mapImages(this.state.albums[pageNumber - 1].imageUrls);
        this.setState({ images });
    }

    render() {
        return (
            <React.Fragment>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>Galerie | MS Renovation</title>
                    <meta name="description" content="Voir nos images de rénovation intérieure." />
                    <meta name="keywords" content="ms, renovation, apartment, paris, france, service, qualité, fr, design, intérieur, galerie, images, gallery" />

                    <link rel="canonical" href="https://ms-renovation.fr/gallery" />
                </Helmet>

                <CustomBanner title={'Galerie'} url={"/gallery"} />

                <div><br /></div>

                <div className="gallery-images">
                    <ImageGallery items={this.state.images}
                        showThumbnails={false}
                        showFullscreenButton={false}
                        showPlayButton={false} />
                </div>

                <div><br /></div>

                <nav className="blog-pagination justify-content-center d-flex">
                    <ul className="pagination">
                        <Pagination
                            activePage={this.state.activePage}
                            itemsCountPerPage={1}
                            totalItemsCount={this.state.albums.length}
                            pageRangeDisplayed={5}
                            itemClass="page-item"
                            linkClass="page-link"
                            onChange={this.onPageChangeHandler.bind(this)}
                        />
                    </ul>
                </nav>
            </React.Fragment>
        );
    }
};

export default GalleryPage;