import React from 'react';
import { Link } from 'react-router-dom';

import ClientReview from '../../components/ClientReview';

const ClientReviews = (_props) => {
    return (
        <section className="testimonial-area pt-100">
            <div className="container">
                <div className="row d-flex justify-content-center">
                    <div className="menu-content pb-70 col-lg-8">
                        <div className="title text-center">
                            <h1 className="mb-10">Avis de nos clients</h1>
                            <p>Si vous ne nous croyez pas, regardez ce que pense notre client.</p>
                            <Link to={'/reviews'} className="primary-btn text-uppercase">Consultez tous les avis clients</Link>
                        </div>
                    </div>
                </div>
                <div className="container" style={{ paddingBottom: 30 }}>
                    <div className="row">
                        <div className="col-sm">
                            <ClientReview
                                text={"Un travail de grande qualité, à temps et à un prix raisonnable. L'équipe MS Renovation a été extrêmement utile. Je les utiliserai certainement pour notre prochaine rénovation et je les recommanderais à tout le monde."}
                                client={"Philippe"}
                            />
                            <div className="col-sm">
                            </div>
                        </div>
                        <div className="col-sm">
                            <ClientReview
                                text={"J'ai utilisé MS Renovation pour les 2 derniers projets - sous-sol fini et rénovation du rez-de-chaussée. Ils sont polis et un plaisir absolu de travailler avec. J'ai recommandé cette entreprise à beaucoup de gens et tous ont de bonnes choses à dire sur lui."}
                                client={"David M."}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default ClientReviews;
