import React from 'react';
import { Link } from 'react-router-dom';

const NavBar = (_props) => {
    return (
        <header id="header">
            <div className="header-top">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6 col-sm-6 col-4 header-top-left no-padding">
                            <a href="tel:0983568616">0983568616</a>                            
                            <a href="mailto:ms.renovation@yahoo.fr">ms.renovation@yahoo.fr</a>
                        </div>
                        <div className="col-lg-6 col-sm-6 col-8 header-top-right no-padding">
                            <ul>
                                <li><Link to={'/'}><i className="fa fa-facebook"></i></Link></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div className="container main-menu">
                <div className="row align-items-center justify-content-between d-flex">
                    <div id="logo">
                        <Link to={'/'}><img src="img/logo.png" alt="MS Renovation logo" title="MS Renovation logo" /></Link>
                    </div>
                    <nav id="nav-menu-container">
                        <ul className="nav-menu">
                            <li><a href="/">Accueil</a></li>
                            <li><a href="/leave-review">Laisser un commentaire</a></li>
                            <li><a href='/reviews' >Avis des clients</a></li>
                            <li><a href='/gallery' >Notre travail</a></li>
                            <li><a href='/contact' >Contact</a></li>
                        </ul>
                    </nav>
                </div>
            </div>
        </header>
    )
};

export default NavBar;