import React from 'react';

import Feature from '../../components/Feature';

const Features = (_props) => {
    return (
        <section className="feature-area section-gap">
            <div className="container">
                <div className="row d-flex justify-content-center">
                    <div className="col-md-12 pb-40 header-text text-center">
                        <h1 className="pb-10 text-white">Quelques caractéristiques qui nous ont rendus uniques</h1>
                        <p className="text-white">
                            C'est pourquoi nous avons développé un modèle de projet éprouvé pour donner vie à ce que chaque client apprécie le plus
                        </p>
                    </div>
                </div>
                <div className="row">
                    <Feature
                        icon={"lnr lnr-user"}
                        title={"Techniciens experts"}
                        text={"Équipe de experts en interne qui ont l'expérience pour savoir ce qui fonctionne le mieux dans chaque situation unique."}
                    />

                    <Feature
                        icon={"lnr lnr-license"}
                        title={"Service professionnel"}
                        text={"Un excellent service client est le travail numéro un dans notre entreprise! C'est la personnalité de l'entreprise et la raison pour laquelle les clients reviennent."}
                    />

                    <Feature
                        icon={"lnr lnr-rocket"}
                        title={"Haute qualité"}
                        text={"Nous pouvons nous vanter de répondre à toutes les exigences avec la qualité avec laquelle nous construisons des maisons pour nos clients."}
                    />

                    <Feature
                        icon={"lnr lnr-bubble"}
                        title={"Commentaires positifs"}
                        text={"Nous sommes fiers de chaque travail, du plus grand au plus petit, et nous nous engageons à satisfaire les clients."}
                    />

                    <Feature
                        icon={"lnr lnr-phone"}
                        title={"Un grand soutien"}
                        text={"Quels que soient vos besoins, quelle que soit l'étape du processus où vous en êtes, nous sommes avec vous."}
                    />

                    <Feature
                        icon={"lnr lnr-diamond"}
                        title={"Hautement recommandé"}
                        text={"Notre engagement envers les projets, terminés à temps et bien faits nous distingue de la concurrence."}
                    />

                </div>
            </div>
        </section >
    );
};

export default Features;