import React, { Component } from 'react';

import ClientReview from '../../components/ClientReview';
import CustomBanner from '../../components/CustomBanner';

import axios from '../../clients/axios';

class ApproveReviewsPage extends Component {

    state = {
        reviews: []
    }

    async componentDidMount() {
        
        const { data: reviews } = await axios.get('/reviews');
        console.log(reviews);
        this.setState({ reviews });
    }

    onApproveReviewHandler = async (id) => {
        try {
            console.log({ id: id });

            await axios.put('/reviews', { id: id });

            const reviews = this.state.reviews.filter(r => r.id !== id);
            this.setState({ reviews });
        } catch(e){}
    }

    onDeleteReviewHandler = async (id) => {
        try {
            await axios.delete(`/reviews/${id}`);

            const reviews = this.state.reviews.filter(r => r.id !== id);
            this.setState({ reviews });
        } catch(e){}
    }

    renderClientReviewRow = _e => {
        const unapprovedReviews = this.state.reviews.filter(review => !review.approved);

        return unapprovedReviews.map(review => {
            return (
                <div key={review.id}
                    className="row"
                    style={{ paddingBottom: 15, margin: 15 }}>
                    <div className="col-sm">
                        <ClientReview
                            text={review.review}
                            client={review.client}
                        />
                        <button onClick={() => this.onApproveReviewHandler(review.id)} className="genric-btn success">Approuver</button>
                        <button onClick={() => this.onDeleteReviewHandler(review.id)} className="genric-btn danger">Supprimer</button>
                    </div>
                </div>
            );
        })
    }

    render() {

        return (
            <React.Fragment>
                <CustomBanner title={'Approuver les avis'} url={"/admin/approve-reviews"} />
                <div className="container" style={{ paddingTop: 30 }}>
                    {
                        this.renderClientReviewRow()
                    }
                </div>
            </React.Fragment>
        );
    }
};

export default ApproveReviewsPage;